function useStations() {
  const { data, error, isLoading } = useSWR(`/Station`);

  if (data && data?.length) {
    return {
      isLoading,
      stations: camelizeKeys(data),
    };
  }

  return {
    isLoading,
    stations: camelizeKeys(STATIONS),
  };
}

export default useStations;
